body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.main-container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    padding-top: 20px;
    margin-right: auto;
    margin-left: auto;
    max-width:1000px;
}

.card-deck {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.card {
    min-width: 300px;
    max-width: 300px;
    margin-top: 20px;
}

.card-title {
    margin-bottom: 0px;
}

.card-img-div {
    display: flex;
    justify-content: center;
}

img.card-img-top {
    width: 60%;
}

p.card-text {
    margin-bottom: 0px;
}

button.close {
    margin-right: 10px;
    outline:none;
}

.modal-body {
    display: flex;
    justify-content: center;
}

#drop-zone {
    border: 2px dashed #bbb;
    border-radius: 5px;
    padding: 25px;
    text-align: center;
    color: #bbb;
}

.thumb {
    height: 75px;
    margin: 10px 5px 0 0;
}

.fab{
	width: 64px;
	height: 64px;
	border-radius: 50%;
	/* text-align:center; */
	color: #FFF;
	position: fixed;
	bottom: 48px;
	right: 48px;
  font-size: 30px;
  outline:none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.fab span {
  line-height: 30px;
  height: 32px;
}

.footer-container {
    margin: 20px;
}

